@use 'src/global.module';

h3 {
  color: #fff;
}

svg {
  fill: global.$globalFontColor;
}

.imgSection {
  padding: 25px;
  text-align: center;

  .circleImg {
    width: 120px;
    height: 120px;
    border-radius: 50%;
  }

  .socialSection {
    a {
      margin: 5px;

      svg {
        width: 30px;
        height: 30px;
      }

      img {
        width: 30px;
        height: 30px;
      }
    }
  }
}
